import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Redirect } from 'react-router'
import { logIn, logout } from '../../redux/reducers/auth-reduser'
import Login from './Login'

class LoginContainer extends Component {

    onSubmitLogin = (email, password, rememderMe) => {
        this.props.logIn(email, password, rememderMe)
    }

    // onSubmitLogout = (email, password, rememderMe) => {
    //     logIn(email, password, rememderMe)
    // }

    render() {

        if(this.props.isAuth) return <Redirect to = '/profile' />
        return (
            <Login  {...this.props}/>
        )
    }
}


let mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth,
    error: state.auth.error,
    errorMessage: state.auth.errorMessage

})

export default connect(mapStateToProps, {logIn, logout})(LoginContainer)