import React from 'react';
import s from './MyPosts.module.css';
import Post from './post/Post';
import { Field, reduxForm } from 'redux-form';
import { maxLengthCreator, required } from '../../../utils/validators';
import { Textarea } from '../../common/FormsControls';

// const lengthValidate = maxLengthCreator(10)

const maxLength15 = maxLengthCreator(15)

const MyPosts = (props) => {

  const onSubmit = (e) => {
    props.addPost(e.newMessageBody)
  };

  

  return (
    <div style={{ display: 'flex' }}>
      <div className={s.postsBlock}>
        <h2>my posts</h2>
        <MyPostsFormReduser onSubmit={onSubmit} />
        <div className={s.posts}>
          {props.post.post.map((elem, index) => <Post key={index} message={elem.message} likesCount={elem.likesCount} onChen={props.onChen} />)}
        </div>
      </div>
    </div>
  );
};

const MyPostsForm = (props) => {
  
  return (
    <form onSubmit={props.handleSubmit}>
      <div>
        <Field component = {Textarea} name='newMessageBody' placeholder='Enter you message pidor' validate ={[ maxLength15, required]}/>
      </div>
      <div>
        <button>Add Post</button>
      </div>
    </form>

  )
}

const MyPostsFormReduser = reduxForm({
  form: 'post',
})(MyPostsForm)

export default MyPosts;

