import React, { memo } from 'react'
import baseAvatar from '../components/img/1286434.jpg'

const UserInfo = memo((props) => {
    return (
        <>
            <div>
                PROFILE
            </div>
            <div>
                {props.fullName}
            </div>
            { <img src={props.small ? props.small : baseAvatar} alt='a' />}
        </>
    )
})

export default UserInfo
