// import React from 'react'
import React, { Component } from 'react'



class ProfileStatus extends Component {
    state = {
        editMode: false,
        status: this.props.status
    };

    activEditMode = () => {

        this.setState(prev => ({ ...prev, editMode: true }))
    };

    deAtivEditMode = () => {
        this.setState(prev => ({ ...prev, editMode: false }))
        this.props.updteStatus(this.state.status)

    };

    onStatusChange = (e) => {
        this.setState(prev => ({ ...prev, status: e.target.value }))
    };

    componentDidUpdate(prevProps, prevState) {
        if (prevProps.status !== this.props.status) {
            this.setState({
                status: this.props.status
            })
        }
    }

    render() {
        return (
            <div>
                {this.state.editMode ?
                    <div style={{ width: '200px', height: '50px', border: '1px solid' }}>
                        <input onChange={this.onStatusChange} autoFocus={true} onBlur={this.deAtivEditMode} value={this.state.status} />
                    </div> :
                    <div onClick={this.activEditMode} style={{ width: '200px', height: '50px', border: '1px solid green' }}>
                        <span >Статус: {this.props.status === '' ? '---' : this.props.status}</span>
                    </div>}
            </div>
        );
    };
};



export default ProfileStatus
