import React from 'react';
import Preeloder from '../../common/Preeloder/Preeloder';
import s from './ProfileInfo.module.css'
import ProfileStatus from './ProfileStatus';
import daseAva from '../../../redux/avatarIng/rONxYDD5qbI.jpg'
import ProfileStatusHooks from './ProfileStatusHooks';

const ProfileInfo = (props) => {
  return (
    <div>
      <div >
        {/* <div>
          <img alt="a" src='https://a.d-cd.net/8613059s-960.jpg'></img>
        </div> */}
        <div className={s.description}>
          {props.profile !== null ? <><div><p><span >Об о мне: </span>{props.profile.fullName}</p></div><img src={props.profile.photos.small !== null ? props.profile.photos.small : daseAva} alt=''></img></> : <Preeloder />}
          <ProfileStatus {...props} />
          <ProfileStatusHooks {...props} />
        </div>
      </div>
    </div>
  )
}

export default ProfileInfo;

