import React from 'react'
import { NavLink } from 'react-router-dom';
import s from './NewUsers.module.css';
import baseImg from '../components/img/1286434.jpg'

function NewUsers(props) {
    let countPage = props.totalCount / props.pageSize;
    let arrPage = [];
    for (let i = 1; i <= countPage; i++) {
        arrPage.push(i)
    };

    return (
        <div>
            {arrPage.map(item =>
                <span onClick={() => {
                    props.setNumberPage(item)
                    props.onPageChenge(item)
                }} className={item === props.pageNumber ? s.selected : ''} key={item}>{item}</span>)
            }
            {props.users.map(item =>
                <div key={item.id}>
                    <h1>{item.name}</h1>
                    <NavLink to={'./newProfile/' + item.id}><img src={item.photos.small === null ? baseImg : item.photos.small} alt="2" /></NavLink>
                    <div>
                        {item.followed

                            ? <button disabled={props.disable.some(id => id === item.id)} onClick={() => {
                                props.disableAC(item.id)
                                props.unFollowRT(item.id)
                            }}>UnFollow</button>
                            : <button disabled={props.disable.some(id => id === item.id)} onClick={() => {
                                props.disableAC(item.id)
                                props.followRT(item.id)
                            }
                            }>Follow</button>}
                    </div>
                </div>
            )}
        </div>
    )
}

export default NewUsers
