import React, { Component } from 'react';
import { connect } from 'react-redux';
import NewHeader from './NewHeader';
import { getAuthRT, myLogout } from '../redux/reducers/newAuthReducer';

class NewHeaderContainer extends Component {

    componentDidMount() {       
        if(!this.props.profile.isAuth) return this.props.getAuthRT()
    };

    render() {
        
        return (
            <NewHeader {...this.props} />
        );
    };
};


let mapStateToProps = (state) => {
    return {
        profile: state.profileAuth
    };
};

export default connect(mapStateToProps, { getAuthRT, myLogout })(NewHeaderContainer);