import { followApi, usersApi } from "../../api/api";
import { updateObjectInArray } from "../../utils/object-helpers";
import { FOLLOW, SET_CURRENT_PAGE, SET_TOTAL_COUNT, SET_USER, TOGGLE_IS_FETCHING, TOGGLE_IS_FOLLOWING_PROGRESS, UNFOLLOW } from "../actions/acrtionsType";
import { setFetching, setTotalCount, setUsers, unFollow, toggleFollowingProgress, follow } from "../actions/actionCreator";

const initialState = {
    users: [],
    pageSize: 10,
    totalUsersCount: 0,
    currentPage: 1,
    isFeching: false,
    followingInProgress: []
}

export default function userReduser(state = initialState, action) {
    switch (action.type) {
        case TOGGLE_IS_FETCHING:
            return { ...state, isFeching: action.isFetching }
        case SET_TOTAL_COUNT:
            return { ...state, totalUsersCount: action.totalCount }
        case SET_CURRENT_PAGE:
            return { ...state, currentPage: action.currentPage }
        case FOLLOW:
            return {
                ...state,
                users: updateObjectInArray(state.users, action.id, "id", { followed: true })
                // users: state.users.map(item => {
                //     if (item.id === action.id) {
                //         return { ...item, followed: true }
                //     }
                //     return item
                // })
            }
        case UNFOLLOW:
            return {
                ...state,
                users: updateObjectInArray(state.users, action.id, "id", { followed: false })
                // users: state.users.map(item => {
                //     if (item.id === action.id) {
                //         return { ...item, followed: false }
                //     }
                //     return item
                // })
            }
        case SET_USER:
            return {
                ...state, users: action.users
            }
        case TOGGLE_IS_FOLLOWING_PROGRESS:
            return {
                ...state,
                followingInProgress: action.isFetching
                    ? [...state.followingInProgress, action.userId]
                    : state.followingInProgress.filter(item => item !== action.userId)
            }
        default: return state;
    }
}

const followUnfollowFlow = async (dispatch, e, apiMethod, actionCreator) => {
    dispatch(toggleFollowingProgress(true, e));
    let res = await apiMethod(e);
    if (res.data.resultCode === 0) {
        dispatch(actionCreator(e))
    };
    dispatch(toggleFollowingProgress(false, e));

}

export const getUsersThunkCreator = (currentPage, pageSize) => async (dispatch) => {
    dispatch(setFetching(true));
    let res = await usersApi.getUsers(currentPage, pageSize);
    dispatch(setFetching(false));
    dispatch(setUsers(res.items));
    dispatch(setTotalCount(res.totalCount));
};

