import axios from "axios";

let instance = axios.create({
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    withCredentials: true,
    headers: {
        "API-KEY": "4c71dd1e-5882-40af-9b58-9e6b4ed64528"
    }
}
)

export const getProfileAPI = {
    profile(e) {
        return instance.get(`profile/${e}`)
    },
    getStatus(userId) {
        return instance.get(`/profile/status/${userId}`)
    },
    upsateStatus(status) {
        return instance.put(`/profile/status`, { status })
    }
}

export const newUserAPI = {
    getMyUsers(currentPage, pageSize) {
        return instance.get(`users?page=${currentPage}&count=${pageSize}`)
            .then(res => res.data)
    }
}

export const followUnfollow = {
    follow(e) {
        return instance.post(`follow/${e}`)
    },
    unFollow(e) {
        return instance.delete(`follow/${e}`)
    }
}

export const authAPI = {
    me() {
        return instance.get('auth/me')
    },
    login(email, password, rememberMy) {
        return instance.post('auth/login', { email, password, rememberMy })
            
    },
    logout(){
        return instance.delete('auth/login')
    }
}