import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Redirect } from 'react-router'
import { myLoginRT } from '../redux/reducers/newAuthReducer'
import MyLoginForm from './MyLoginForm'

function MyLogin() {
    
    let dispatch = useDispatch()
    let value = useSelector(state => state.profileAuth)
    const onSub = (data) => {
        dispatch(myLoginRT(data.login, data.password, data.remembermy))
        return dispatch(myLoginRT(data.login, data.password, data.remembermy))
    }

    if(value.isAuth) return <Redirect to='/newProfile' />

    return (
        <>
            <h1>MyLoginasd</h1>
            <MyLoginForm onSub={onSub} />
        </>
    )
}



export default MyLogin


