import React from 'react'
import { Form, Field } from 'react-final-form'
import { myRequired  } from './../utils/myValidator'
import MyFormControl from '../components/common/MyFormControl'


let Input = MyFormControl('input')

const composeValidators = (...validators) => value => {
    return validators.reduce((error, validator) => error || validator(value), undefined)
}


const MyLoginForm = (props) => (
    <Form

        onSubmit={props.onSub}
        render={({ handleSubmit, submitError }) => {
            return (
            <form onSubmit={handleSubmit}>
                <h2>Form Login</h2>
                <label>Login</label>
                <div>
                    <Field name='login' component={MyFormControl('input')} validate={composeValidators(myRequired)} />
                </div>
                <div>
                    <Field name='password' component={Input} type='password' autoComplete="off" validate={composeValidators(myRequired)} />
                </div>
                <button type ='submit'>Login</button>
                {submitError && <span>{submitError}</span>}
            </form>
        )}}
    >
    </Form>
)





export default MyLoginForm
