import { Field, reduxForm } from 'redux-form';
import { required } from '../../utils/validators';
import { Input } from '../common/FormsControls';
import s from '../common/MyFormControl.module.css'





function LoginForm(props) {
    return (
        <form onSubmit={props.handleSubmit}>
            <div>
                <Field name={'email'} placeholder={"Login"} component={Input} validate={[required]} />
            </div>
            <div>
                <Field name={'password'} placeholder={"Password"} component={Input} validate={[required]} />
            </div>
            <div>
                <Field name={'rebemberMe'} type={"checkbox"} component={Input} /> rebember me
            </div>
            { props.error &&
                <div className={s.formError}>
                    {props.error}
                </div>
            }
            <div>
                <button>Login</button>
            </div>
        </form>)
}

const LoginReduxForm = reduxForm({ form: 'login' })(LoginForm)

export default LoginReduxForm