import React, { Component } from 'react';
import './App.css';
import NavBar from './components/NavBar/NavBar';
import { BrowserRouter, Route } from 'react-router-dom';
import News from './components/News/News';
import Setting from './components/setting/Setting';
import Music from './components/Music/Music';
// import DialogsContainer from './components/Diaplogs/DialogsContainer';
// import UsersContainer from './components/Users/UsersContainer';
import ProfileContainer from './components/Profile/ProfileContainer';
import HeaderContainer from './components/Header/HeaderContainer';
import NewUsersContainer from './workoutComponent/NewUsersContainer';
import NewProfileContainer from './workoutComponent/NewProfileContainer';
import NewHeaderContainer from './workoutComponent/NewHeaderContainer';
import MyLogin from './workoutComponent/MyLogin';
import NewMessagePageContainer from './workoutComponent/NewMessagePageContainer';
import LoginContainer from './components/Login/LoginContainer';
import { connect } from 'react-redux';
import { authMyRT, logout } from './redux/reducers/auth-reduser';
import { setAuthUserData } from './redux/actions/actionCreator';
import Preeloder from './components/common/Preeloder/Preeloder';
import { initializeApp } from './redux/reducers/app-reduser';
import withSuspens from './hoc/withSuspens';

const UsersContainer = React.lazy(() => import('./components/Users/UsersContainer'));
const DialogsContainer = React.lazy(() => import('./components/Diaplogs/DialogsContainer'));





class App extends Component {

  componentDidMount() {

    this.props.initializeApp()

  }


  render() {
    if (!this.props.initialized) {
      return <Preeloder />
    }
    return (
      <BrowserRouter>
        <div className='app-wrapper'>
          <NewHeaderContainer />
          <HeaderContainer />
          <NavBar />
          <div className='app-wrapercontent'>
            <Route path='/dialogs' render={withSuspens(DialogsContainer)}/>
            <Route path='/profile/:userId?' render={() => <ProfileContainer />} />
            <Route path='/news' component={News} />
            <Route path='/music' component={Music} />
            <Route path='/setting' component={Setting} />
            <Route path='/users'
                   render={withSuspens(UsersContainer)} />
            <Route path='/newUsers' render={() => <NewUsersContainer />} />
            <Route path='/newProfile/:userId?' render={() => <NewProfileContainer />} />
            <Route path='/login' render={() => <LoginContainer />} />
            <Route path='/mylogin' render={() => <MyLogin />} />
            <Route path='/newmessage' render={() => <NewMessagePageContainer />} />
          </div>
        </div>
      </BrowserRouter>
    )
  };
};

let mapStateToProps = (state) => ({
  isAuth: state.auth.isAuth,
  login: state.auth.login,
  initialized: state.app.initialized
})


export default connect(mapStateToProps, { setAuthUserData, authMyRT, logout, initializeApp })(App);


