import { SET_NEW_TOTAL_COUNT, SET_ARR_USER, SET_NUMBER_PAGE, GET_PROFILE, SET_AUTH, ADD_NEW_MESSAGE, ADD_POST, CHENGE_MESSAGE_DIALOG, CHENGE_NEW_POST_TEXT, FOLLOW, SET_CURRENT_PAGE, SET_TOTAL_COUNT, SET_USER, SET_USER_DATE, SET_USER_PROFILE, TOGGLE_IS_FETCHING, TOGGLE_IS_FOLLOWING_PROGRESS, UNFOLLOW, TOGGLE_FOLLOW, DISABLE, CLEAR_DISABLE, SET_STATUS, MY_GET_STATUS, MY_ADD_POST, MY_ADD_MESSAGE, ERROR_PASS, SET_INITIALIZED, MY_SET_INITIALIZED,  DELETE_POST } from "./acrtionsType";

export const addPostActionCreator = (message) => ({ type: ADD_POST, message });

export const addNewMessageActionCreator = (message) => ({ type: ADD_NEW_MESSAGE, message });

export const updatePostTextActionCreator = (e) => ({ type: CHENGE_NEW_POST_TEXT, text: e.target.value });

export const updateMessageDialog = (e) => ({ type: CHENGE_MESSAGE_DIALOG, text: e.target.value });

export const follow = id => ({ type: FOLLOW, id });

export const unFollow = (id) => ({ type: UNFOLLOW, id });

export const setUsers = (users) => ({ type: SET_USER, users });

export const selectedPage = (currentPage) => ({ type: SET_CURRENT_PAGE, currentPage });

export const setTotalCount = (totalCount) => ({ type: SET_TOTAL_COUNT, totalCount });

export const setFetching = (isFetching) => ({ type: TOGGLE_IS_FETCHING, isFetching });

export const setUserProfile = (profile) => ({ type: SET_USER_PROFILE, profile });

export const setAuthUserData = (userId, email, login, isAuth) => ({ type: SET_USER_DATE, data: { email, userId, login, isAuth } });

export const toggleFollowingProgress = (isFetching, userId) => ({ type: TOGGLE_IS_FOLLOWING_PROGRESS, isFetching, userId })

export const deletePost = (id) => ({type: DELETE_POST, id})




export const setStatus = (status) => ({ type: SET_STATUS, status })

export const myGetStatusAC = (status) => ({ type: MY_GET_STATUS, status })

export const myAddPost = (message) => ({ type: MY_ADD_POST, message })

export const myAddMessage = (message) => ({ type: MY_ADD_MESSAGE, message })

export const errorPass = (errorMessage, error) => ( {type: ERROR_PASS, payload: {errorMessage, error} })

export const setInitializedSuccess = () => ({type:SET_INITIALIZED})




export const setNewTotalCount = (totalCount) => ({ type: SET_NEW_TOTAL_COUNT, totalCount: totalCount });

export const setUserArr = (users) => ({ type: SET_ARR_USER, users: users });

export const setNumberPage = e => ({ type: SET_NUMBER_PAGE, pageNumber: e });

export const getProfile = e => ({ type: GET_PROFILE, profile: e })

export const setAuthUser = (id, login, email, isAuth) => ({ type: SET_AUTH, data: {id, login, email, isAuth} })

export const toggleFollow = id => ({ type: TOGGLE_FOLLOW, id })

export const progressFollower = e => ({})

export const disableAC = id => ({ type: DISABLE, id })

export const clearDisable = id => ({ type: CLEAR_DISABLE, id })

export const mySetInitializedSuccess = () => ({type: MY_SET_INITIALIZED})