import s from './MyFormControl.module.css'

const MyFormControl = (Element) => ({ input, meta, ...props }) => {
    const hasError = meta.touched && meta.error
    return (
        <>
            <div>{meta.submitError}</div>
            <Element className={hasError ? s.error + ' ' + s.input : ''} {...input} {...props}></Element> {hasError && <span className={`material-icons + ' ' + ${s.material_icons}`}>error_outline</span>}
            {hasError && <div><span>{meta.error}</span></div>}
        </>
    )
}

