import React from 'react'
import { Suspense } from 'react'
import Preeloder from '../components/common/Preeloder/Preeloder'

function withSuspens(Component) {

    return (props) => {
        return <Suspense fallback={<Preeloder />}><Component {...props} /></Suspense>
    }
}

export default withSuspens
