import React, { Component } from 'react'
import { connect } from 'react-redux'
import NewUsers from './NewUsers'
import { setNewTotalCount, setUserArr, setNumberPage, toggleFollow, disableAC, clearDisable } from '../redux/actions/actionCreator';
import { getMyUsersRT } from '../redux/reducers/newProfile-reducers';
import { followRT, unFollowRT } from '../redux/reducers/newProfile-reducers'
import MyWithAuthRedirect from '../hoc/MyWithAuthRedirect';
import { compose } from 'redux';

class NewUsersContainer extends Component {

    componentDidMount() {
        this.props.getMyUsersRT(this.props.pageNumber, this.props.pageSize)
    };

    onPageChenge = (e) => {
        this.props.getMyUsersRT(e, this.props.pageSize)
    };

    render() {
        return (
            <div>
                <NewUsers {...this.props} onPageChenge={this.onPageChenge} />
            </div>
        );
    };
};

const mapStateToProps = (state) => {
    return {
        auth: state.profileAuth.isAuth,
        totalCount: state.newState.totalCount,
        pageSize: state.newState.pageSize,
        pageNumber: state.newState.pageNumber,
        users: state.newState.users,
        disable: state.newState.disable
    }
}



// export default connect(mapStateToProps, { setNewTotalCount, setUserArr, setNumberPage, toggleFollow, disableAC, clearDisable, getMyUsersRT, followRT, unFollowRT})(MyWithAuthRedirect(NewUsersContainer))

export default compose(
    connect(mapStateToProps, { setNewTotalCount, setUserArr, setNumberPage, toggleFollow, disableAC, clearDisable, getMyUsersRT, followRT, unFollowRT }),
    MyWithAuthRedirect
)(NewUsersContainer)