import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { updteStatus } from '../../../redux/reducers/profile-reducer';

function ProfileStatusHooks(props) {
    
    const state = useSelector(state => state.profilePage.status);

    const dispatch = useDispatch();
    const [editMode, setEditMode] = useState(false);
    const [status, setStatus] = useState(state);

    useEffect(() => {
        setStatus(state);
    }, [state]);

    const activeMode = () => {
        setEditMode(true);
    };

    const deActiveMode = () => {
        setEditMode(false);        
        dispatch(updteStatus(status));
    };

    const onChenge = (e) => {
        setStatus(e.currentTarget.value);
    };

    return (
        <div>
            {editMode ?
                <div   style={{ width: '200px', height: '50px', border: '1px solid' }}>
                    <input onChange = {(e) => onChenge(e)} autoFocus = {true} onBlur = {deActiveMode} value={status} />
                </div> :
                <div onClick = {activeMode} style={{ width: '200px', height: '50px', border: '1px solid green' }}>
                    <span >Статус: {props.status === '' ? '---' : props.status}</span>
                </div>}
        </div>
    );
};

export default ProfileStatusHooks;
