
import { FORM_ERROR } from "final-form"
import { login, profile } from "../../api/api"
import { authAPI } from "../../api/newApi"
import { SET_AUTH } from "../actions/acrtionsType"
import { setAuthUser, setUserProfile } from "../actions/actionCreator"

const initialState = {
    email: "",
    id: null,
    login: "",
    isAuth: false,
    status: ''

}

export default function newAuthReducer(state = initialState, action) {
    switch (action.type) {
        case SET_AUTH:
            return { ...state, ...action.data }
        default: return state
    }
}

export const getProfileRT = (e) => {
    return (dispatch) => {
        profile.getProfile(e)
            .then(res => {
                dispatch(setUserProfile(res.data))
            })
    }
}


export const getAuthRT = () => {
    return (dispatch) => authAPI.me()
        .then(res => {
            if (res.data.resultCode === 0) {
                let { id, login, email } = res.data.data
                dispatch(setAuthUser(id, login, email, true))
            }
        })
}

export const myLoginRT = (email, password, rememberMy) => async (dispatch) => {
    login.login(email, password, rememberMy, true)
        .then(res => {
            if (res.data.resultCode === 0) {
                dispatch(getAuthRT())
            } else {
                debugger
                return { [FORM_ERROR]: res.data.messages[0] }
            }
        })
}

export const myLogout = () => (dispatch) => {
    return authAPI.logout()
        .then(res => {
            if (res.data.resultCode === 0) {
                dispatch(dispatch(setAuthUser(null, null, null, false)))
            }
        })
}