import { profile } from "../../api/api";
import { ADD_POST, CHENGE_NEW_POST_TEXT, DELETE_POST, SET_STATUS, SET_USER_PROFILE } from "../actions/acrtionsType";
import { setStatus } from "../actions/actionCreator";

const initialState = {
    post: [
        { id: 1, message: "1 Hi, how are you?", likesCount: 9 },
        { id: 2, message: "2 it's my first post", likesCount: 15 },
        { id: 3, message: "3 it's my first post", likesCount: 15 },
        { id: 4, message: "4 it's my first post", likesCount: 15 }
    ],
    newPostText: "it-kimasuter.com",
    profile: null,
    status: '',
}

const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_PROFILE:
            return { ...state, profile: action.profile }
        case ADD_POST:
            return { ...state, post: [...state.post, { id: 3, message: action.message, likesCount: 0 }], newPostText: action.message }
        case CHENGE_NEW_POST_TEXT:
            return { ...state, newPostText: action.text }
        case SET_STATUS:
            return { ...state, status: action.status }
        case DELETE_POST:
            return { ...state, post: state.post.filter(item => item.id !== action.id) }
        default: return state;
    }
}

export default profileReducer;

export const getStatus = (userId) => async (dispatch) => {
    let res = await profile.getStatus(userId)
    dispatch(setStatus(res.data))
}

export const updteStatus = (status) => async (dispatch) => {
    let res = await profile.updateStatus(status)
    if (res.data.resultCode === 0) {
        dispatch(setStatus(status))
    }
}
