import React, { Component } from 'react'
import { connect } from 'react-redux'
import { compose } from 'redux'
import MyWithAuthRedirect from '../hoc/MyWithAuthRedirect'
import { myAddMessage } from '../redux/actions/actionCreator'
import NewMessagePage from './NewMessagePage'

class NewMessageContainer extends Component {

    render() {
        return (
            <NewMessagePage {...this.props} />
        )
    }
}

let mapStateToProps = (state) => ({
    message: state.myMessagePage.message
})

let mapDispatchToProps = (dispatch) => ({
    adMessage: (e) => dispatch(myAddMessage(e))
})

export default compose(
    connect(mapStateToProps, mapDispatchToProps),
    MyWithAuthRedirect
)(NewMessageContainer)