import React, { Component } from 'react';
import Profile from './Profile';
import { connect } from 'react-redux';
import { setUserProfile } from '../../redux/actions/actionCreator';
import { withRouter } from 'react-router';
import { getProfileRT } from '../../redux/reducers/newAuthReducer';
import { compose } from 'redux';
import { getStatus, updteStatus } from '../../redux/reducers/profile-reducer';

class ProfileContainer extends Component {

    componentDidMount() {
        let userId = this.props.match.params.userId || this.props.userId;
        if (userId) {
            this.props.getProfileRT(userId);
            this.props.getStatus(userId);
        } else this.props.history.push("/login")
    };

    render() {
        return (
            <>
                <Profile {...this.props} />
            </>
        );
    };
};

let mapStateToProps = (state) => ({
    profile: state.profilePage.profile,
    status: state.profilePage.status,
    authorizedUserId: state.auth.userId,
    isAuth: state.auth.isAuth,
    userId: state.auth.userId

});

export default compose(
    connect(mapStateToProps, { setUserProfile, getProfileRT, getStatus, updteStatus }),
    // withAuthRedirect,
    withRouter
)(ProfileContainer)