import axios from "axios";


const instance = axios.create({
    withCredentials: true,
    baseURL: "https://social-network.samuraijs.com/api/1.0/",
    headers: {
        "API-KEY": "4c71dd1e-5882-40af-9b58-9e6b4ed64528"
    }
});

export const usersApi = {
    getUsers(currentPage = 1, pageSize = 10) {
        return instance.get(`users?page=${currentPage}&count=${pageSize}`)
            .then(res => {
                return res.data
            }
            );
    }
}


export const followApi = {
    follow(e) {
        return instance.post(`https://social-network.samuraijs.com/api/1.0/follow/${e}`)
    },
    unFollow(e) {
        return instance.delete(`https://social-network.samuraijs.com/api/1.0/follow/${e}`)
    }
}

export const login = {
    me(){
        return instance.get('auth/me')
    },
    login(email, password, rememderMe = false){
        return instance.post('auth/login', {email, password, rememderMe })
    },
    logout(){
        return instance.delete('auth/login')
    }
}

export const profile = {
    getProfile(e){
        return instance.get(`https://social-network.samuraijs.com/api/1.0/profile/${e}`)
    },
    getStatus(userId){
        return instance.get(`/profile/status/${userId}`)
    },
    updateStatus(status){
        return instance.put('/profile/status', { status})
    }
}

export const statusAPI = {
    status(e){
        instance.put(`/profile/status/${e}`)
    }
}

