import React from 'react';
import { NavLink } from 'react-router-dom';
import s from './NavBar.module.css'

const NavBar = (props) => {


  return (
    <div className={s.nav}>
      <nav >
        <ul>
          <li className={s.item}><NavLink to='/login' activeClassName={s.active}>Login</NavLink></li>
          <li className={s.item}><NavLink to='/mylogin' activeClassName={s.active}>MyLogin</NavLink></li>
          <li className={s.item}><NavLink to='/profile' activeClassName={s.active}>Profile</NavLink></li>
          <li className={s.item}><NavLink to='/dialogs' activeClassName={s.active}>Message</NavLink></li>
          <li className={s.item}><NavLink to='/news' activeClassName={s.active}>Nevs</NavLink></li>
          <li className={s.item}><NavLink to='/Music' activeClassName={s.active}>Music</NavLink></li>
          <li className={s.item}><NavLink to='/Setting' activeClassName={s.active}>Settings</NavLink></li>
          <li className={s.item}><NavLink to='/users' activeClassName={s.active}>Users</NavLink></li>
          <li className={s.item}><NavLink to='/newUsers' activeClassName={s.active}>NewUsers</NavLink></li>
          <li className={s.item}><NavLink to='/newProfile' activeClassName={s.active}>NewProfile</NavLink></li>
          <li className={s.item}><NavLink to='/newmessage' activeClassName={s.active}>NewMessage</NavLink></li>
        </ul>
      </nav>
      <h2 className={s.headsFriend}>Friends</h2>
    </div>

  )
}

export default NavBar;

