import React from 'react';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';

let mapStateToPropsRedirect = (state) => ({
    isAuth: state.profileAuth.isAuth
})


function MyWithAuthRedirect(Components) {    

    class WithRedirect extends React.Component {

        render() {
            if (!this.props.isAuth) return <Redirect to={'/mylogin'} />
            return <Components {...this.props} />
        }
    }
    
    let ConnectAuthRedirectComponent = connect(mapStateToPropsRedirect, {})(WithRedirect)

    return ConnectAuthRedirectComponent;
}

export default MyWithAuthRedirect

