import { ADD_NEW_MESSAGE } from "../actions/acrtionsType"


const initialState = {
    dialogsName: [
        { id: 1, name: 'Vasiliy' },
        { id: 2, name: 'Mary' },
        { id: 3, name: 'Oleg' },
        { id: 4, name: 'Ivan' },
        { id: 5, name: 'Ilya' },
        { id: 6, name: 'Sanya' }
    ],
    message: [
        { id: 1, message: 'Hi1' },
        { id: 2, message: 'Hi2 привет пидор' },
        { id: 3, message: 'Hi3' },
        { id: 4, message: 'Hi4' },
        { id: 5, message: 'Hi5' },
        { id: 6, message: 'Hi6' }
    ]
}

const dialogsReduser = (state = initialState, action) => {
    switch (action.type) {
        case ADD_NEW_MESSAGE:
            return { ...state, message: [...state.message, { id: 7, message: action.message }] }
        default: return state
    }
}

export default dialogsReduser