import React from 'react'
import { NavLink } from 'react-router-dom';
import s from './NewHeader.module.css';

const NewHeader = (props) => {
    return (
        <div className={s.header}>
            <h1>worcout HEADER </h1>
            {/* <span>{props.profile.login}</span> */}
            <img src='https://amgames.ru/images/MMO/BD/stt/basic/kat2.png' alt="1"></img>
            
            <div className={s.loginBlock}>
                {props.profile.isAuth ? <div>{props.profile.login}<button onClick = { props.myLogout}>Logout</button></div> : <NavLink to ={'/login'}>Login</NavLink>}
            </div>
        </div>

    )
}

export default NewHeader
