import React from 'react';
import s from './Post.module.css';

const Post = (props) => {
  return (
    <div className={s.item}>
      <img src='https://million-wallpapers.ru/wallpapers/5/51/424703866779568/nejtiri-2017-avatara-2.jpg' alt="h1"></img>
          {props.message}
      <div>
        <span>Like </span>{props.likesCount}
      </div>
    </div>
  )
}

export default Post;

