import { followUnfollow, getProfileAPI, newUserAPI } from "../../api/newApi";
import { CLEAR_DISABLE, DISABLE, GET_PROFILE, MY_ADD_POST, MY_GET_STATUS, SET_ARR_USER, SET_NEW_TOTAL_COUNT, SET_NUMBER_PAGE, TOGGLE_FOLLOW } from "../actions/acrtionsType";
import { clearDisable, getProfile, myGetStatusAC, setNewTotalCount, setUserArr, toggleFollow } from "../actions/actionCreator";

let initState = {
    post: [
        { id: 1, message: "Hi, how are you?", likesCount: 9 },
        { id: 2, message: "it's my first post", likesCount: 15 }
    ],
    totalCount: null,
    pageSize: 10,
    users: [],
    pageNumber: 1,
    progressFolowed: true,
    disable: [],
    status: ""

}

export default function newProfileReducers(state = initState, action) {
    switch (action.type) {
        case SET_NEW_TOTAL_COUNT:
            return { ...state, totalCount: action.totalCount }
        case SET_ARR_USER:
            return { ...state, users: action.users };
        case SET_NUMBER_PAGE:
            return { ...state, pageNumber: action.pageNumber }
        case GET_PROFILE:
            return { ...state, profile: action.profile };
        case TOGGLE_FOLLOW:
            return {
                ...state, users: state.users.map(item => {
                    if (item.id === action.id) {
                        return { ...item, followed: !item.followed }
                    }
                    return item
                }
                )
            }
        case DISABLE:
            return { ...state, disable: [...state.disable, action.id] }
        case CLEAR_DISABLE:
            return { ...state, disable: state.disable.filter(id => id === !action.id) }
        case MY_GET_STATUS:
            return { ...state, status: action.status }
        case MY_ADD_POST:
            return { ...state, post: [...state.post, { id: 2, message: action.message, likesCount: 15 }] }
        default: return state
    }
}

export const getMyUsersRT = (pageNumber, pageSize) => {
    return (dispatch) => {
        newUserAPI.getMyUsers(pageNumber, pageSize).then(res => {
            dispatch(setNewTotalCount(res.totalCount))
            dispatch(setUserArr(res.items))
        });
    }
}


export const unFollowRT = (e) => {
    return dispatch => {
        followUnfollow.unFollow(e)
            .then(res => {
                if (res.data.resultCode === 0) {
                    dispatch(toggleFollow(e))
                };

                dispatch(clearDisable(e))
            })
    }
}

export const followRT = (e) => {
    return dispatch => {
        followUnfollow.follow(e)
            .then(res => {
                if (res.data.resultCode === 0) {
                    dispatch(toggleFollow(e))
                };

                dispatch(clearDisable(e))
            })
    }
}

export const getProfileRT = (e) => {
    return (dispatch) => {
        getProfileAPI.profile(e)
            .then(res => {
                dispatch(getProfile(res.data))
            })
    }
}

export const myGetStatus = (itemId) => {
    return (dispatch) => {
        getProfileAPI.getStatus(itemId)
            .then(res => dispatch(myGetStatusAC(res.data)))
    }
}

export const setUpdeteStatus = (status) => (dispatch) => {
    getProfileAPI.upsateStatus(status)
        .then(res => {
            if (res.data.resultCode === 0) {
                // myGetStatusAC(res.data.message)
                dispatch(myGetStatusAC(status))
            }
        })
}