import { applyMiddleware, combineReducers, compose, createStore } from "redux";
import AuthReduser from "./reducers/auth-reduser";
import dialogsReduser from "./reducers/dialogs-reducer";
import newAuthReducer from "./reducers/newAuthReducer";
import profileReducer from "./reducers/profile-reducer";
import sidebarReducer from "./reducers/sidebar-reducer";
import userReduser from "./reducers/user-reducer";
import thunkMiddleware from 'redux-thunk';
import { reducer as formReducer } from 'redux-form'
import newProfileReducers from "./reducers/newProfile-reducers";
import newDialogsReduser from "./reducers/newDialog-reducer";
import appReducer from "./reducers/app-reduser";
import myAppReducer from "./reducers/myApp-reducer";

let reducers = combineReducers({
    profileAuth: newAuthReducer,
    newState: newProfileReducers,
    myMessagePage: newDialogsReduser,
    myApp: myAppReducer,

    profilePage: profileReducer,
    messagePage: dialogsReduser,
    sidebar: sidebarReducer,
    usersPage: userReduser,
    auth: AuthReduser,
    app: appReducer,
    form: formReducer
});

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(reducers, composeEnhancers(applyMiddleware(thunkMiddleware)));

// let store = createStore(reducers, applyMiddleware(thunkMiddleware));



export default store;