import React from 'react'
import { Form, Field } from 'react-final-form'
import MyFormControl from '../components/common/MyFormControl'
import { myMaxLength, myRequired } from '../utils/myValidator'

let Input = MyFormControl('textarea')

const composeValidators = (...validators) => value => {
    return validators.reduce((error, validator) => error || validator(value), undefined)
}

function PostMessagesForm(props) {
    return (
        <Form
        initialValues ={{messageBody: props.init}}
            onSubmit={values => {
                props.myAddPost(values.messageBody)
            }}>
            {({ handleSubmit }) => (
                <form onSubmit={handleSubmit}>
                    <div>
                        <Field validate = {composeValidators(myRequired, myMaxLength(15))} component={Input} name='messageBody' placeholder='Enter you message' />
                    </div>
                    <div>
                        <button>add</button>
                    </div>
                </form>
            )}

        </Form>
    )
}



export default PostMessagesForm
