import React, { memo, useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { setUpdeteStatus } from '../redux/reducers/newProfile-reducers';

const MyProfileStatus = memo((props) => {

    const [state, setstate] = useState({ editMode: false, status: props.status })
    let dispatch = useDispatch()

    let editActiv = () => {
        setstate(prev => ({ ...prev, editMode: true }))
        if (state.status !== props.status) {
            setstate(prev => ({ ...prev, status: props.status }))
        }
    }

    let editOff = () => {
        setstate(prev => ({ ...prev, editMode: false }))
        dispatch(setUpdeteStatus(state.status))
    }

    const chengeInput = (e) => {
        setstate(prev => ({ ...prev, status: e.target.value }))

    }

    useEffect(() => {
    }, [state])


    return (
        <div>
            {state.editMode
                ? <input autoFocus = {true} onChange={chengeInput} onBlur={editOff} value={state.status} />
                : <div style={{ width: '150px', height: '50px', border: '1px solid red' }} onClick={editActiv}>{props.status}</div>}

        </div>
    )
})

export default MyProfileStatus
