import React from 'react'
import NewMessagePageForm from './NewMessagePageForm';
import NewMessages from './NewMessages';

function NewMessage(props) {
    return (
        <div>
            <h1>Message</h1>
            <NewMessagePageForm adMessage = {props.adMessage}/>
            {/* <PostMessagesForm /> */}
            <NewMessages message = {props.message}/>
        </div>
    )
}

export default NewMessage
