import MyPosts from './MyPost';
import { connect } from 'react-redux';
import { addPostActionCreator } from '../../../redux/actions/actionCreator';

const mapStateProps = (state) => {
    return {
        profilePage: state.profilePage.newPostText,
        post: state.profilePage,
        newPostText: state.profilePage.newPostText
    }
}

const mapDispatchToProps = (dispatch) => {

    return {
        addPost: (e) => {
            dispatch(addPostActionCreator(e));
        }
    }
}

const MyPostsContainer = connect(mapStateProps, mapDispatchToProps,)(MyPosts)

export default MyPostsContainer;

