import { MY_SET_INITIALIZED } from "../actions/acrtionsType"
import { getAuthRT } from "./newAuthReducer"
import {mySetInitializedSuccess} from "../actions/actionCreator"

let initialState = {
    initialized: false
}

const myAppReducer = (state = initialState, action) => {
    switch (action.type) {
        case MY_SET_INITIALIZED:
            return { ...state, initialized: true }
        default: return state
    }
}

export default myAppReducer;

export const myInitializeApp = () => (dispatch) => {
    let propmise = dispatch(getAuthRT())
    Promise.all([propmise])
    .then(() => {
        dispatch(mySetInitializedSuccess())
    })

    
}