import React from 'react'
import { Form, Field } from 'react-final-form';
import MyFormControl from '../components/common/MyFormControl';
import { myMaxLength, myRequired } from '../utils/myValidator';

let Input = MyFormControl('textarea')

const composeValidators = (...validators) => value => {
    return validators.reduce((error, validator) => error || validator(value), undefined)
}

function NewMessagePageForm(props) {
    return (
        <Form 
        onSubmit = {(value) => {
            props.adMessage(value.mesageBody)
        }}
        render = {({handleSubmit, submitError}) => (
            <form onSubmit = {handleSubmit}>
                <Field name = 'mesageBody' component = {Input} placeholder = 'entet' validate = {composeValidators(myMaxLength(15), myRequired)}/>
                <button>Send</button>
                {submitError && <div>{submitError}</div>}
            </form>
        )}
        />
    )
}

export default NewMessagePageForm
