import { stopSubmit } from "redux-form";
import { login } from "../../api/api";
import { ERROR_PASS, SET_USER_DATE } from "../actions/acrtionsType";
import { errorPass, setAuthUserData } from "../actions/actionCreator";

let initialState = {
    email: null,
    userId: null,
    login: null,
    isAuth: false,
    error: false,
    errorMessage: ''

};

const AuthReduser = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATE:
            return { ...state, ...action.data }
        case ERROR_PASS:
            return { ...state, errorMessage: action.payload.errorMessage, error: action.payload.error }
        default: return state
    }
}

export default AuthReduser;

export const authMyRT = () => async (dispatch) => {
    let res = await login.me();
    if (res.data.resultCode === 0) {

        let { email, id, login } = res.data.data
        dispatch(setAuthUserData(id, email, login, true))
    }

}


export const logIn = (email, password, rememderMe) => async (dispatch) => {

    let res = await login.login(email, password, rememderMe)
    if (res.data.resultCode === 0) {
        dispatch(authMyRT())
        dispatch(errorPass('', false))
    }
    else {
        dispatch(stopSubmit("login", { _error: res.data.messages.length > 0 ? res.data.messages[0] : "Some Error" }))
    }
}

export const logout = () => async (dispatch) => {
    let res = await login.logout()
    if (res.data.resultCode === 0) {
        dispatch(dispatch(setAuthUserData(null, null, null, false)))
    }
}
