import React, { Component } from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router'
import { compose } from 'redux'
import Preeloder from '../components/common/Preeloder/Preeloder'
import { myAddPost } from '../redux/actions/actionCreator'
import { getProfileRT, myGetStatus } from '../redux/reducers/newProfile-reducers'
import NewPost from './NewPost'
import NewProfile from './NewProfile'
import PostMessagesForm from './PostMessagesForm'

class NewProfileContainer extends Component {

    componentDidMount() {
        let userID = this.props.match.params.userId || this.props.id

        if (userID) {
            this.props.getProfileRT(userID)
            this.props.myGetStatus(userID)
        } else {
            return this.props.history.push("/login")
        }
    };

    render() {
        return (
            <>
                {this.props.profile ? <NewProfile {...this.props} profile={this.props.profile} /> : <Preeloder />}
                <PostMessagesForm myAddPost={this.props.myAddPost} />
                <NewPost post={this.props.post} />
            </>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        profile: state.newState.profile,
        profileAuth: state.progileAuth,
        isAuth: state.auth.isAuth,
        status: state.newState.status,
        post: state.newState.post,
        id: state.profileAuth.id
    }
}


export default compose(
    connect(mapStateToProps, { getProfileRT, myGetStatus, myAddPost }),
    withRouter,
)(NewProfileContainer)